import React from "react";
import SectionHeader from "./SectionHeader";
import styled from "styled-components";
import logo from "../logo.svg";
import mePic from "../lukeProfile.png"

const Container = styled.div`
  /* display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 20px; */
  /* flex-direction: row; */
  width: 80%;
  /* justify-content: space-evenly; */
  /* align-items: center; */
`;

const AboutParagraph = styled.div`
  flex-grow: 2;
`;

const AppLogo = styled.img`
  /* margin: 10px; */
  height: 128px;
  width: 128px;
  pointer-events: none;
  border-left: 10px solid white;
  float: left;
  /* margin: 0 10px 10px 0; */
  margin-right: 10px;
`;

function AboutMe() {
  // TODO: Figure out how to make the image resize and appear center beside the text, also seems like the text is center but not the image?
  return (
    <React.Fragment>
      <SectionHeader text="About Me" />
      <Container>
        <AppLogo src={mePic} alt="logo" />
        <AboutParagraph>
          Hello! I am Luke Chamberlain, a senior software developer at Klipfolio.
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
          minim veniam, quis nostrud exercitation ullamco laboris nisi ut
          aliquip ex ea commodo consequat. Duis aute irure dolor in
          reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla
          pariatur. Excepteur sint occaecat cupidatat non proident, sunt in
          culpa qui officia deserunt mollit anim id est laborum.
        </AboutParagraph>
      </Container>
    </React.Fragment>
  );
}

export default AboutMe;
